.videoWidth{
  height: 100vh !important;
  width: 100vw !important;
  overflow-y: hidden !important; /* Hide vertical scrollbar */
  overflow-x: hidden !important; 
}

video{
  object-fit:fill;
  height: 100vh !important;
  width: 100vw !important;
}


.sidechannel{
  font-size: 20px;
  margin: 10px 20px;
  align-items: center;
  height: 50px;
  line-height: 50px;
}

.logo{
  float: right;
  position: relative;
  right: 0;
}

.b{
  border-bottom: 1px dotted #000;
  cursor: pointer;
}

.m2{
  margin-left: 20px;
}

/* width */
::-webkit-scrollbar {
  overflow: auto;
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #88888850; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}